<template>
  <button @click="handleClick" class="el-button" :type="nativeType" :disabled="disabled"
  :class="[type ? 'el-button--' + type : '',buttonSize ? 'el-button--' + buttonSize : '',
  {'is-disabled': disabled}]">
    <span v-if="$slots.default"><slot></slot></span>
  </button>
</template>
<script>
  export default {
    data() {
      return {
        
      }
    },
    props: {
      disabled: Boolean,
      nativeType: {
        type: String,
        default: 'button'
      },
      size: String,
      type: {
        type: String,
        default: 'default'
      }
    },
    computed: {
      buttonSize() {
        return this.size;
      }
    },
    methods: {
      handleClick(evt) {
        this.$emit('click',evt)
      }
    }
  }
</script>
<style>
</style>



